<template>
  <div class="ruleBoxMain" v-show="data.isShow">
    <div style="display: flex;justify-content: space-between;align-items: center">
      <h5 style="margin-left: 10px;">Rule{{number}}</h5>
      <span class="el-icon-close" @click="deleteMe(number)"></span>
    </div>
    <div class="ruleBox">
      <div class="selectField">
        <el-select v-model="data.selectField" slot="prepend" placeholder="Select field">
          <el-option label="field1" value="field1"></el-option>
          <el-option label="field2" value="field2"></el-option>
          <el-option label="field3" value="field3"></el-option>
        </el-select>
      </div>
      <div class="selectOperator" >
        <el-select v-model="data.selectOperator" slot="prepend" placeholder="Select operator">
          <el-option label="operator1" value="operator1"></el-option>
          <el-option label="operator2" value="operator2"></el-option>
          <el-option label="operator3" value="operator3"></el-option>
        </el-select>
      </div>
      <div class="enterParameter" ref="parameter">
        <div class="enterParameterUnit" v-for="number in parameterAmount" :key="number">
          <el-input v-model="data.inputRule[number-1]" placeholder="Enter parameter"></el-input>
          <img class="operation" @click="addParameter" style="width: 20px;height: 20px" v-if="number===1" :src="'./imgs/add_circle_outline.svg'" alt="">
          <img class="operation" @click="removeParameter(number-1)" style="width: 20px;height: 20px" v-else :src="'./imgs/remove.svg'" alt="">
        </div>
      </div>
    </div>
    <div style="display: flex;align-items: center;margin-top: 10px;">
      <span style="margin-left: 10px;">The revenue is</span>
      <el-input style="width: 250px;margin-left: 20px;" v-model="data.revenueAmount" placeholder="% Enter amount"></el-input>
    </div>
  </div>
</template>

<script>
export default {
  name: "RuleUnit",
  mounted() {

  },
  data() {
    return {
      parameterAmount:1,
      data:{
        selectField:undefined,
        selectOperator:undefined,
        isShow:true,
        inputRule:[],
        revenueAmount:'',
      }
    }
  },
  props:['number'],
  methods:{
    addParameter() {
      if(this.parameterAmount===3) {
        alert('The maximum rules is 3')
      }
      else  {
        this.parameterAmount++;
      }
    },
    removeParameter(child) {
      this.data.inputRule.splice(child,1);
      this.parameterAmount--;
    },
    deleteMe() {
      this.data.isShow = false;
    }
  },
  watch:{
    data:{
      handler(newVal,oldVal) {
        this.$emit('changeData',{newVal,number:this.number});
      },
      deep:true,
      immediate:true,
    }
  }
}
</script>

<style scoped lang="less">
.ruleBoxMain {
  width: 1200px;
  height: 250px;
  border: 2px dashed gray;
}
.ruleBox {
  width: 1170px;
  height: 150px;
  background-color: rgba(239, 239, 239);
  margin-top: 10px;
  margin-left: 10px;
  display: flex;
  .selectField,.selectOperator,.enterParameter {
    margin-top: 10px;
    margin-left: 10px;
  }
  .enterParameterUnit {
    display: flex;
    align-items: center;
  }
}
.operation:hover {
  cursor: pointer;
}
</style>
