import Vue from 'vue'
import VueRouter from 'vue-router'
import CreateRevenueGroup from "@/views/CreateRevenueGroup";
import BrowseRevenueGroup from "@/views/BrowseRevenueGroup";
import ShowList from "@/views/ShowList";
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name:'CreateRevenueGroup',
    component: CreateRevenueGroup,
  },
  {
    path: '/browseRevenueGroup',
    name:'BrowseRevenueGroup',
    component: BrowseRevenueGroup,
  },
  {
    path: '/showList',
    name: 'ShowList',
    component: ShowList,
  }
]

const router = new VueRouter({
  routes
})

export default router
