<template>
  <div>
    <h2 class="title">Create Revenue Group</h2>
    <div class="header">
      <h4 style="margin-bottom: 10px;">Group Name</h4>
      <el-input v-model="RevenueGroupData.name" placeholder="Name"></el-input>
      <h4>Group Description</h4>
      <el-input
          type="textarea"
          :rows="2"
          :maxlength="200"
          placeholder="Add description"
          v-model="RevenueGroupData.headerTextArea">
      </el-input>
      <el-checkbox style="margin-top: 10px" v-model="RevenueGroupData.isSpecialGroup">Special Group</el-checkbox>
    </div>
    <div class="rulesMain">
      <div class="rulesHeader">
        <h4>Rules</h4>
        <el-button class="addRuleBtn" type="primary" @click="addRule" icon="el-icon-plus">Add</el-button>
      </div>
      <div class="ruleContent">
        <RuleUnit v-on:changeData="changeData" v-for="(item,index) in RevenueGroupData.ruleData.length"
                  style="margin-top: 10px;"
                  :key="item.id"
                  :number="index+1"
        ></RuleUnit>
        <div style="display: flex;margin-top: 20px;">
          <el-button @click="refresh" round>Reset</el-button>
          <el-button type="primary" round @click="submit">Submit</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import RuleUnit from "@/components/RuleUnit";

export default {
  name: "CreateRevenueGroup",
  components: {
    RuleUnit,
  },
  mounted() {

  },
  data() {
    return {
      RevenueGroupData: {
        name: '',
        headerTextArea: '',
        isSpecialGroup: false,
        ruleNumber: 1,
        ruleData: [{
          id: Math.random() * Math.random(),
        }],
      }
    }
  },
  methods: {
    addRule() {
      this.RevenueGroupData.ruleNumber++;
      console.log('add');
      this.RevenueGroupData.ruleData.push({
        id: Math.random() * Math.random(),
      });
    },
    changeData(obj) {
      this.RevenueGroupData.ruleData[obj.number - 1] = obj.newVal;
    },
    submit() {
      console.log(this.RevenueGroupData.ruleData);
      console.log(this.RevenueGroupData.ruleData);
      for (let i = 0; i < this.RevenueGroupData.ruleData.length; i++) {
        if(!this.RevenueGroupData.ruleData[i].isShow) {
          this.RevenueGroupData.ruleData.splice(i,1);
          i--;
        }
      }
      this.$store.commit('addRevenue',(this.RevenueGroupData));
      this.$router.push({
        name: 'ShowList',
      })
    },
    refresh() {
      location.reload();
    }
  },
  watch: {}
}
</script>

<style scoped lang="less">
.title {
  margin: 10px;
  font-weight: 400;
}

.header {
  margin: 30px 10px 10px 10px;

  h4 {
    font-weight: 400;
    margin-top: 20px;
    margin-bottom: 10px;
  }
}

.rulesMain {
  margin: 30px 10px 10px;

  .rulesHeader {
    display: flex;
    justify-content: space-between;

    .addRuleBtn {
      border-radius: 20px;
    }
  }

  .ruleContent {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
}
</style>
