<template>
  <div>
    <div style="margin: 10px;display: flex;align-items: center">
      <h2>{{ title }}</h2>
      <div style="width: 130px;height: 30px;background-color:skyblue;text-align: center;border-radius: 10px;margin-left: 10px;" v-if="isSpecial">Special Group</div>
      <img style="margin-left: 20px" class="delete" @click="handleClick2()" :src="'./imgs/Iconsdelete.svg'" alt="">
    </div>
    <p>{{ description }}</p>
    <el-table
        empty-text="Empty Data"
        :data="tableData"
        stripe
        style="width: 100%">
      <el-table-column
          prop="number"
          label="Rule"
          width="180">
      </el-table-column>
      <el-table-column
          prop="selectField"
          label="Field"
          width="180">
      </el-table-column>
      <el-table-column
          prop="selectOperator"
          label="Operator">
      </el-table-column>
      <el-table-column
          prop="inputRule[0]"
          label="Parameter1">
      </el-table-column>
      <el-table-column
          prop="inputRule[1]"
          label="Parameter2">
      </el-table-column>
      <el-table-column
          prop="inputRule[2]"
          label="Parameter3">
      </el-table-column>
      <el-table-column
          prop="revenueAmount"
          label="Revenue">
      </el-table-column>
      <el-table-column
          fixed="right"
          label="Action"
          width="100">
        <template slot-scope="scope">
          <img class="delete" @click="handleClick(scope.$index)" :src="'./imgs/Iconsdelete.svg'" alt="">
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "BrowseRevenueGroup",
  data() {
    return {
      tableData: [],
      title: '',
      description: '',
      isSpecial: false,
    }
  },
  mounted() {
    console.log(this.$route.params.RevenueGroupData)
    this.tableData = this.$route.params.RevenueGroupData.ruleData;
    this.title = this.$route.params.RevenueGroupData.name;
    this.description = this.$route.params.RevenueGroupData.headerTextArea;
    this.isSpecial = this.$route.params.RevenueGroupData.isSpecialGroup;
    for (let i = 0; i < this.tableData.length; i++) {
      this.tableData[i].number = i + 1;
    }
  },
  methods: {
    handleClick(row) {
      this.tableData.splice(row,1);
    },
    handleClick2() {
      this.$eventBus.$emit('deleteShowList',this.$route.params.number);
      this.$router.push({
        name:'ShowList',
        params:{
          number:this.$route.params.number,
        }
      })
    }
  }
}
</script>

<style scoped>
.delete:hover {
  cursor: pointer;
}

 p {
  margin: 10px;
}

p {
  margin-bottom: 20px;
}
</style>
