<template>
  <div>
    <el-button @click="$router.push({name:'CreateRevenueGroup'})" type="primary" style="margin: 15px" round>Create Revenue Page</el-button>
    <el-card @click.native="goDetail(index)" class="box-card" v-for="(item,index) in data" :key="index"
             style="width: 400px;margin: 20px;">
      <div slot="header" class="clearfix">
        <span>Name: {{ item.name }}</span>
      </div>
      <div>
        Description:{{ item.headerTextArea }}
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "ShowList",
  mounted() {
    this.data = this.$store.state.Revenue;
    this.$eventBus.$on('deleteShowList',(num)=>{
      this.data.splice(num,1);
    })
  },
  data() {
    return {
      data: [],
    }
  },
  methods: {
    goDetail(index) {
      console.log(this.data[index]);
      this.$router.push({
        name: 'BrowseRevenueGroup',
        params: {
          RevenueGroupData: this.data[index],
          number:index,
        },
      })
    },
  }
}
</script>

<style scoped>
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both
}

.box-card {
  width: 480px;
}

.box-card:hover {
  cursor: pointer;
}
</style>
